* {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
}

html,
body {
  height: 100%;
  font-family: "Roboto", system-ui, "Segoe UI", Helvetica, Arial, sans-serif,
    "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol";
}

body {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 10px;
  background: rgb(34, 193, 195);
  background: linear-gradient(
    0deg,
    rgba(34, 193, 195, 1) 0%,
    rgba(86, 142, 191, 1) 100%
  );
}

.container {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  gap: 40px;
  padding: 10px;
  background-color: #00000080;
  color: #fff;
  border-radius: 10px;
}

.search-box {
  width: 100%;
  display: flex;
  justify-content: flex-end;
}

form input,
form button {
  padding: 15px 20px;
  border: 1px solid #000000;
  border-radius: 5px;
  background-color: #e5e5ff;
}

form button {
  padding: 15px 10px;
  font-size: 14px;
  font-weight: bold;
  color: #e5e5ff;
  background: rgb(34, 193, 195);
  background: linear-gradient(
    0deg,
    rgba(34, 193, 195, 1) 0%,
    rgba(86, 142, 191, 1) 50%
  );
}

form button:hover {
  background: rgb(34, 193, 195);
  background: linear-gradient(
    180deg,
    rgba(34, 193, 195, 1) 0%,
    rgba(86, 142, 191, 1) 100%
  );
}

form input:focus {
  padding: 7px 10px;
}

.currentWeather {
  width: 100%;
  display: flex;
  flex-wrap: wrap;
  justify-content: left;
}

.forecastContainer {
  display: flex;
  justify-content: left;
  gap: 10px;
  flex-wrap: wrap;
}
